<template>
  <div>
    <div v-if="false">
      <el-upload
        class="avatar-uploader"
        action="https://jsonplaceholder.typicode.com/posts/"
        :show-file-list="false"
        :on-success="handleAvatarSuccess"
        :before-upload="beforeAvatarUpload"
      >
        <img v-if="imageUrl" :src="imageUrl" class="avatar">
        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
      </el-upload>
    </div>

    <div v-else>
      <el-upload
        action="http-request"
        :http-request="uploadSectionFile"
        list-type="picture-card"
        :on-preview="handlePictureCardPreview"
        :on-remove="handleRemove"
        :on-success="handleSuccess"
        :on-exceed="handleExceed"
        :limit="limit"
        :file-list="files"
        multiple
      >
        <i class="el-icon-plus"></i>
      </el-upload>
      <el-dialog :visible.sync="dialogVisible">
        <img width="100%" :src="dialogImageUrl">
      </el-dialog>
    </div>
    
    <image-compressor-cmp ref="imageCompressorCmp" />
  </div>
</template>

<script>
import imageCompressorCmp from '@/components/image_compressor_cmp/index.vue'

export default {
  name: 'uploadImgCmp',

  props: {
    limit: {
      type: Number,
      default: 1,
    },
    webUrl: {
      type: Array,
      default: () => {
        return []
      }
    }
  },

  data() {
    return {
      dialogImageUrl: '',
      dialogVisible: false,
      fileList: [],
      files: [],
    }
  },

  components: {
    imageCompressorCmp
  },

  created () {
    this.files = this.webUrl.map((item) => {
      return { url: item }
    })
  },

  methods: {
    handleRemove(file, fileList) {
      this.$emit('removeIndex', this.fileList.indexOf(JSON.stringify(file)))
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url
      this.dialogVisible = true
    },
    handleSuccess (response, file, fileList) {
      let fileListSuccess = fileList
      this.fileList = fileListSuccess.map((item) => {
        return JSON.stringify(item)
      })
    },
    handleExceed (files, fileList) {
      this.$message.error(`最多只能上传${this.limit}张图片`)
    },
    // 自定义覆盖图片上传方法
    async uploadSectionFile (params) {
      const isJPG = params.file.type === 'image/jpeg' || params.file.type === 'image/png'
      const isLt2M = params.file.size / 1024 / 1024 < 10
      if (!isJPG) {
        this.$message.error('上传头像图片只能是 JPG/PNG 格式!')
        return
      }
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 10MB!')
        return
      }
      const formData = await this.$refs.imageCompressorCmp._imageCompressor(params.file)
      await this._upload(formData)
    },

    // --------------------------------------------------------------------------------------
    // 上传图片
    async _upload (formData) {
      const res = await this.$http.post(this.$api.uploadFile, formData)
      this.$emit('childImg', res.data.data.webUrl)
    },
  }
}
</script>

<style lang="scss" scoped>
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>
