<template>
  <Layout-box one="绑定主播">
    <el-form ref="form" :model="form" size="small" label-width="80px">
      <el-form-item label="主播：">
        <el-select v-model="form.anchorId" placeholder="请选择">
          <el-option
            v-for="item in tableData"
            :key="item.id"
            :label="item.realName"
            :value="item.id"
          >
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="协议：">
        <upload-img-cmp :limit="3" @childImg="onChildImg" @removeIndex="onRemoveIndex" />
      </el-form-item>
      <el-form-item>
        <el-button @click="onSubmit">提交</el-button>
      </el-form-item>
    </el-form>
  </Layout-box>
</template>

<script>
import LayoutBox from '@/components/LayoutBox/index'
import uploadImgCmp from '@/components/upload_img_cmp/index'

export default {
  name: 'addShopZhuBo',

  components: {
    LayoutBox,
    uploadImgCmp
  },

  data () {
    return {
      form: {
        anchorId: '', // 主播id
        protocol: [], // 协议
      },
      tableData: [],
    }
  },

  async created () {
    await this.apiQueryAnchor()
  },

  methods: {
    // 点击提交
    async onSubmit () {
      await this.apiAnchorAllInsert()
    },
    // 获取子组件的图片
    onChildImg (e) {
      this.form.protocol.push(e)
    },
    // 获取删除的子组件图片下标
    onRemoveIndex (e) {
      this.form.protocol.splice(e, 1)
    },

    // -----------------------------------------------------------
    // 主播注册申请列表查询
    async apiQueryAnchor () {
      const data = {
        pageSize: 999,
        status: 1
      }
      const res = await this.$http.post(this.$api.anchorAllQuery, data)
      this.tableData = res.data.data
    },
    // 提交
    async apiAnchorAllInsert () {
      let protocol = this.form.protocol.join(',')
      const data = {
        ...this.form,
        protocol: protocol
      }
      const res = await this.$http.post(this.$api.anchorAllInsert, data)
      if (res.data.success) {
        this.$message.success('提交成功')
        this.$router.go(-1)
      } else {
        this.$message.error(res.data.msg)
      }
    }
  }
}
</script>

<style lang="scss" scoped>

</style>
